.application{
    flex-direction: row;
    display: flex;
    background-color: white;
    border-radius: 14px;
    overflow: hidden;
    margin: 10px;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.08) 0 2px 6px 0;
    flex: 1 1;
}

.applicationRow{
    display: flex;
}

.imageContainer{
    width: 90px;
    height: 90px;
    border-radius: 14px;
    border-width: 1px;
    border-color: rgb(230,230,230);
    border-style: solid;
    margin: 10px;
    overflow: hidden;
}

.image{
    width: 100%;
}

.metadata{
    flex: 1 1;
    justify-content: space-around;
    display: flex;
    flex-direction: column;
}

.title{
    font-size: 14px;
    font-weight: 500;
}

.subtitle{
    font-size: 12px;
    margin: 6px 0 6px 0;
    color: #555;
    max-height: 42px;
    overflow-y: scroll;
}

.bottomContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.price{
    font-size: 12px;
    color: brown;
}

.link{
    font-size: 12px;
    color: cornflowerblue;
    text-decoration: none;
}

.download{
    margin: 10px;
    font-size: 12px;
    font-weight: 500;
    border-color: rgb(33, 150, 243);
    color: rgb(33, 150, 243);
    border-style: solid;
    border-radius: 30px;
    border-width: 1px;
    padding: 4px 6px;
    text-decoration: none;
}